/* Microsoft Theme CSS */

.Navbar {
  background-color: #333;
  padding: 10px;
}

.NavbarContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.NavbarList {
  list-style: none;
  display: flex;
  gap: 20px;
}

.NavbarItem {
  font-size: 16px;
  color: #fff;
  cursor: pointer;
  transition: color 0.3s ease;
}

.NavbarItem:hover {
  color: #0078d7;
}

.active {
  color: #0078d7;
}
