.AboutMe {
    padding: 20px 0;
    background-color: #f9f9f9;
  }
  
  .AboutMeContent {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .AboutMeImage {
    flex: 1;
    max-width: 300px;
    margin-right: 40px;
  }
  
  .AboutMeImage img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 3px solid #0078d7;
    box-shadow: 0 0 10px rgba(0, 120, 215, 0.3);
  }
  
  .AboutMeText {
    flex: 1;
  }
  
  .AboutMeText h2 {
    
    margin-bottom: 20px;
  }
  
  .AboutMeText p {
    font-size: 1.2rem;
    line-height: 1.6;
    color: #666;
  }
  
  @media (max-width: 768px) {
    .AboutMeContent {
      flex-direction: column;
      align-items: center;
    }
    
    .AboutMeImage {
      margin-right: 0;
      margin-bottom: 20px;
    }
  }

  h2{
    text-align: center;
    font-size: 2.5rem;
  }