/* Custom Line Breaker CSS */

.LineBreaker {
    position: relative;
    width: 100%;
    height: 1px;
    margin: 30px 0;
    background-color: #999;
  }
  
  .LineBreaker::before {
    content: "";
    position: absolute;
    top: -5px;
    left: 50%;
    transform: translateX(-50%);
    width: 10px;
    height: 10px;
    background-color: #999;
    border-radius: 50%;
  }
  
  .LineBreaker::after {
    content: "";
    position: absolute;
    top: -5px;
    left: 50%;
    transform: translateX(-50%);
    width: 10px;
    height: 10px;
    background-color: #999;
    border-radius: 50%;
  }
  
  